import React, { useState, useEffect } from "react";
import './Footer.css'

function formatDate(date) {
    if (!date) return "";
    const hours = `0${date.getHours()}`.slice(-2);
    const minutes = `0${date.getMinutes()}`.slice(-2);
    const seconds = `0${date.getSeconds()}`.slice(-2);
    return `${hours}:${minutes}:${seconds}`;
}

function Footer() {
    const [timeString, setTimeString] = useState("");
    useEffect(() => {
        const clockInterval = setInterval(() => {
            const now = new Date();
            const newTimeString = formatDate(now);

            setTimeString(newTimeString);
        }, 1000);
        return () => {
            clearInterval(clockInterval);
        };
    }, []);
    return (
        <div className='footer'>
            <div className='footer-top py-3'>
                <div className='footer-service pb-2'>
                    <h5 className="widget-title">V CHNG TI</h5>
                    <p>Bn &amp; sa cha in thoi </p>
                    <p>Hng dn mua hng</p>
                    <p>Chnh sch bo hnh</p>
                    <p>Thanh ton</p>
                    <p>Chng trnh khuyn mi</p>
                </div>
                <div className='footer-contact pb-2'>
                    <h5>THNG TIN LIN H</h5>
                    <p>a ch: 168 Lng Th Vinh, Thanh Xun, H Ni</p>
                    <p>S in thoi: 0987216425</p>
                    <p>Email: mobilestore25@gmail.com</p>
                    <p>Website: mobilestore.net</p>
                </div>
                <div className='footer-fanpage pb-2'>
                    <h5>FANPAGE</h5>

                    <div className="fb-page"
                        data-href="https://www.facebook.com/profile.php?id=100068839505063"
                        data-width="380"
                        data-hide-cover="false"
                        data-show-facepile="false"></div>
                </div>
            </div>
            <div className='footer-bottom'>
                <div className='footer-policy'>
                    <p style={{ margin: "0" }}>2021 Mobile Store</p>
                </div>
                <div className='footer-time'>
                    <p style={{ margin: "0" }}>{timeString}</p>
                </div>
            </div>
        </div>
    )
}

export default Footer